   /* General reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, sans-serif;
  background-color: #f9f9f9;
  color: #333;
}

/* Header container */
.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 20px;
  background-color: #fff;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.1);
}

.header-logo {
  font-size: 24px;
  font-weight: bold;
}

.header-logo span {
  color: #ff007a; /* Highlighted pink text */
}

/* Search bar */
.header-search {
  display: flex;
  align-items: center;
  flex: 1;
  max-width: 600px;
  margin: 0 20px;
}

.header-search input {
  flex: 1;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 20px;
  outline: none;
}

.header-search button {
  background: none;
  border: none;
  cursor: pointer;
  margin-left: -30px;
  color: #666;
}

/* Navigation */
.header-nav {
  display: flex;
  align-items: center;
  gap: 20px;
}

.header-nav a {
  text-decoration: none;
  color: #666;
  font-size: 16px;
  transition: color 0.3s;
}

.header-nav a:hover {
  color: #000;
}

.basket-icon {
  font-size: 20px;
}

/* Banner */
.banner {
  background: linear-gradient(to right, #333, #d14343);
  color: #fff;
  padding: 20px;
  border-radius: 10px;
  margin: 20px;
}

.banner-title {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 5px;
}

.banner-text {
  font-size: 16px;
}

.banner-link {
  font-weight: bold;
  color: #fff;
  text-decoration: underline;
  cursor: pointer;
}
