/*Footer Styling */
.footer {
  background-color: #edf0f2;
  color: #333;
  font-family: Arial, sans-serif;
  padding: 30px 50px;
  line-height: 1.5;
}

.footer-sections {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
}

.footer-column h4 {
  font-weight: bold;
  margin-bottom: 10px;
}

.footer-column ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.footer-column li {
  margin-bottom: 5px;
  cursor: pointer;
}

.footer-column li:hover {
  text-decoration: underline;
}

/* Socials and Signup */
.footer-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 30px;
}

.socials {
  display: flex;
  gap: 15px;
}

.socials svg {
  font-size: 20px;
  color: #333;
  cursor: pointer;
}

.signup {
  display: flex;
  gap: 10px;
  align-items: center;
}

.signup input {
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  flex: 1;
  min-width: 250px;
}

.signup button {
  padding: 8px 15px;
  background-color: #333;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.signup button:hover {
  background-color: #555;
}

/* Payment Section */
.payment-section {
  text-align: center;
}

.payment-icons {
  display: flex;
  justify-content: center;
  gap: 15px;
  margin-top: 15px;
}

.payment-icons img {
  width: 40px;
  height: auto;
}

/* Footer Links */
.footer-links {
  text-align: center;
  margin-top: 20px;
}

.footer-links p {
  margin: 0;
}

.footer-links a {
  color: #333;
  text-decoration: none;
  margin: 0 5px;
}

.footer-links a:hover {
  text-decoration: underline;
}
