body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
}

.product-card {
  border: 1px solid #ddd;
  padding: 10px;
  margin: 10px;
  text-align: center;
}

.product-list {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.search-bar {
  margin: 20px;
  display: flex;
  gap: 10px;
}

button {
  cursor: pointer;
  padding: 5px 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
}

